.open {
  position: relative;
}

.open .slide {
  left: 0;
}
.slide {
  font-size: 20px;
  opacity: 0;
  animation: ani 0.5s forwards;
}

@keyframes ani {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.sidebarItem {
}
.sidebarItem:hover {
  background-color: rgb(243, 243, 243);
  border-radius: 5px;
}
.selected {
  background-color: blue !important;
  color: white;
  border-radius: 5px;
}
.buttonContainer {
  display: flex;
}
.buttonContainer:hover {
  background-color: rgb(243, 243, 243);
  border-radius: 5px;
}
.clothingContainer {
  display: flex;
}
