.main {
  background-color: black;
  height: 100%; /* Измените это на 100%, чтобы .main занимал только доступное пространство */
  width: 100%;
  position: relative;
}

.slider {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex; /* Change this from block to flex */
  margin: 0 auto; /* Add this to center the slider */
  padding: 0;
  align-items: center; /* Add this to vertically center the slides */
  justify-content: center; /* Add this to horizontally center the slides */
}

.image {
  width: 100%;
  height: 100%; /* Измените это на 100%, чтобы .image занимал всю высоту .slider */
  object-fit: fill; /* Используйте это, чтобы сохранить пропорции изображения */
}

.left_arrow {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  opacity: 0.5;
}
.left_arrow:hover {
  opacity: 1;
}
.right_arrow {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  opacity: 0.5;
}
.right_arrow:hover {
  opacity: 1;
}

.slide {
  opacity: 0;
  transition-duration: 1s ease;
}

.slide.active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}

.button_container {
  display: flex;
  justify-content: space-between;
  position: absolute;
  width: 98%;
}
.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 1s ease;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 1s ease;
}
