.open {
    position: relative;
  }
  
  .open .slide {
    left: 0;
  }
  .slide{
    font-size: 20px;
    opacity: 0;
    animation: ani 0.5s forwards;
  }
  
  @keyframes ani {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }
  
