.clothingContainer {
  padding: 10px 0 10px 0;
}
/* Стили для тега <a> */
.clothingContainer a {
  /* Убираем подчеркивание ссылки */
  text-decoration: none;
  /* Задаем цвет ссылки */
  color: black;
}

/* Стили для блока с меню */
.menu {
  /* Скрываем блок по умолчанию */
  display: none;
  /* Задаем фоновый цвет для блока */
  background-color: white;
  /* Задаем отступы для блока */
  padding: 10px;
  /* Задаем границу для блока */
  border: 1px solid black;
  /* Задаем позицию для блока */
  position: absolute;
  /* Смещаем блок вниз относительно ссылки */
  top: 50px;

  z-index: 10;
}

/* Стили для компонентов внутри блока */
.menu > * {
  /* Задаем отступы для компонентов */
  margin: 10px;
}

/* Стиль для отображения блока при наведении на ссылку или на блок */
.clothingContainer a:hover + .menu,
.menu:hover {
  /* Изменяем свойство display на block */
  display: block;

  animation: ani 0.5s forwards;

  opacity: 0;
}

@keyframes ani {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
