.recommended-flex {
  display: flex;
  margin-left: 20rem;
  gap: 7px;
}
.button {
  height: 42px;
border: none;
background: #3a7999;
color: #f2f2f2;
padding: 10px;
font-size: 18px;
border-radius: 5px;
position: relative;
box-sizing: border-box;
transition: all 500ms ease; 

}
.button:hover {
  background-color: rgb(76, 165, 255);
}


.recommended-title {
  margin-left: 20rem;
  margin-bottom: 20px;
  margin-top: 20px;
  font-size: 20px;
}
